/* scroll bar */


/* width */

::-webkit-scrollbar {
    width: 10px;
    margin-top: 5rem;
}


/* Track */

::-webkit-scrollbar-track {
    background: rgba(242, 255, 254, 0.2);
}


/* Handle */

::-webkit-scrollbar-thumb {
    background: #cfcfe2;
}


/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
    background: #38384a;
}

.hidden {
    display: none;
}
